.App {
  text-align: center;
  background-color: #282c34;
  color: white;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.section-container {
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
}

.task_input {
  align-items: center;
}

.progress-bar {
  height: 10px;
  background-color: #4caf50;
  margin-bottom: 10px;
}

.task {
  border: 1px solid #ddd;
  padding: 5px;
  margin: 5px;
  cursor: pointer;
}

.completed {
  text-decoration: line-through;
}

/* src/App.css */
.ChecklistProgressBar {
  max-width: 400px;
  margin: auto;
  padding: 20px;
}

.ChecklistProgressBar h2 {
  margin-bottom: 10px;
}

.ChecklistProgressBar label {
  margin-left: 8px;
}

.ChecklistProgressBar progress {
  width: 100%;
  margin: 10px 0;
}

.ChecklistProgressBar progress div {
  height: 100%;
  background: #4caf50;
  transition: width 0.3s ease-in-out;
}

.ChecklistProgressBar div {
  margin-bottom: 8px;
  align-items: center;
}
